 <!--品牌列表-->
<template>
  <div class="cms-list-layout">
    <cms-prompt collapse="true">
      <li>显示品牌列表信息</li>
      <li>可对申请信息进行编辑、删除等操作</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>品牌名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>审核状态</label>
        <el-select v-model="queryParams.status" @change="query">
          <el-option label="全部" :value="''"></el-option>
          <el-option label="待提交" :value="1"></el-option>
          <el-option label="审核中" :value="2"></el-option>
          <el-option label="审核不通过" :value="3"></el-option>
          <el-option label="审核通过" :value="4"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <section class="cms-batch">
      <cms-button
        icon="add"
        type="success"
        size="small"
        @click="$routerLink('/brand/save','save')"
      >新增</cms-button>
    </section>
    <el-table
      :data="tableData"
      style="width: 100%"
      stripe
      v-loading="loading"
      @selection-change="getCheckItems"
      @row-click="checkRows"
      ref="dataTable"
    >
      <el-table-column type="selection" width="46" fixed="left"></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="name" label="品牌名称" min-width="200">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.name">{{scope.row.name}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="initials" label="首字母" min-width="150">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.initials">{{scope.row.initials}}</div>
        </template>
      </el-table-column>
      <el-table-column label="LOGO" min-width="180" prop="logo">
        <div slot-scope="scope" class="table-img">
          <img
            v-if="scope.row.resourcesSpaceData"
            :src="$cms.filterUrl(scope.row.resourcesSpaceData.url) "
            alt="mockimg"
          >
        </div>
      </el-table-column>
      <el-table-column prop="status" label="状态" min-width="110">
        <div slot-scope="scope">
          <span class="gray" v-if="scope.row.status === 1">待提交</span>
          <span class="success" v-if="scope.row.status === 2">审核中</span>
          <span class="error" v-if="scope.row.status === 3">审核不通过</span>
          <span class="success" v-if="scope.row.status === 4">审核通过</span>
        </div>
      </el-table-column>
      <el-table-column prop="remark" label="备注(原因)" min-width="140">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.remark">{{scope.row.remark}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="300" prop="status" fixed="right" align="center">
        <div slot-scope="scope" style="text-align:right">
          <div v-if="scope.row.status === 1||scope.row.status === 3">
            <cms-button icon="edit" size="small" type="info" @click="commit(scope.row.id, 2 )">提交审核</cms-button>
            <cms-button
              icon="edit"
              size="small"
              type="info"
              @click.stop="$routerLink('/brand/edit','update',{id:scope.row.id})"
            >编辑</cms-button>
            <cms-button icon="error" size="small" type="info" @click="deleteData(scope.row.id)">删除</cms-button>
          </div>
          <div v-if="scope.row.status === 2">
            <cms-button icon="edit" size="small" type="info" @click="cancelaudit(scope.row.id)">取消审核</cms-button>
          </div>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total="total"></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  brandList,
  brandCommit,
  brandDelete,
  cancelCommit
} from '@/api/product/brand.js'
var defaultData = {
  name: '',
  status: '',
  page: 1,
  size: 10
}

export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    commit(id, status) {
      brandCommit({ id: id }).then(res => {
        if (res.code === 200) {
          this.$successMessage('提交成功')
          this.getTableData(brandList, this.queryParams)
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    cancelaudit(id) {
      this.$confirm('您确定要取消审核吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          cancelCommit({ id: id }).then(res => {
            if (res.code === 200) {
              this.$successMessage('取消成功')
              this.getTableData(brandList, this.queryParams)
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(() => {})
    },
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(brandDelete, id) // 这个才是真实的删除方法
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(brandList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(brandList, this.queryParams)
  }
}
</script>
<style>
</style>



